@charset "UTF-8";
//轮播图
#goodsDetailBanner {
    height: 10.6rem;
    width: 100%;
    a {
        display: block;
        height: 10.6rem;
        img {
            width: 100%;
            height: 10.6rem;
        }
    }
    .swiper-pagination-bullet {
        width: 0.25rem;
        height: 0.25rem;
        background-color: #fff;
        margin: 0 0.1rem;
        border-radius: 100%;
        display: inline-block;
        &.swiper-pagination-bullet-active {
            background-color: #f60;
        }

    }
    .swiper-pagination {
        width: 100%;
        bottom: 0;
    }
}
//轮播图下方商品信息
.goodsInfo {
    background-color: #fff;
    border-bottom: 0.05rem #D9D9D9 solid;
    .info-box {
        width: 100%;
        padding: 0.6rem 0.5rem 0.3rem 0.5rem;
        overflow: hidden;
        text-align: justify;
        .item-title {
            @extend %text-overflow;
            font-size: 0.75rem;
            height: 1.3rem;
            color: #383838;
        }
        .item-subtitle {
            @extend %text-overflow;
            font-size: 0.6rem;
            color: #ec4619;
            //margin-top: 0.25rem;

            em {
                margin-left:0.2rem;
                font-size: 0.75rem;
            }
            .rmb{
                color: #FF8C24;
                font-size: 0.7rem;
                margin-left: 0.8rem;
                 
            }
            .rmbNumb{
                color: #FF8C24;
                font-size: 0.75rem;
            }
        }
    }
}

//列表样式
.listStylesBox {
    //最外层盒子
    .bottomLine {
        //列表有border-bottom，，没有的话可以去掉
        border-bottom: 0.05rem #D9D9D9 solid;
    }
    .listStyles {
        margin: 0 0.5rem;
        a{
        	min-height: 2rem;
        }
        .item-content {
            padding: 0;
            .item-title {
                font-size: 0.7rem;
            }
            .icon-right {//右箭头
                margin-right: 0.25rem;
                font-size: 0.8rem;
                color: #909090;
            }
        }

    }
    
    .item-size{
        font-size: 0.7rem;
        color: #535353;
        position: absolute;
        right: 1.4rem;
        line-height: 2rem;
    }
    .gd-size{
        width: 65%;
        white-space: nowrap;
        overflow-x: scroll;
    }
    
    .item-num{
        position: absolute;
        right: 1.4rem;
        color: #FF8C24;
        font-size: 0.7rem;
        line-height: 2rem;
            .rmb{
                
//              font-size: 0.7rem;
                margin-left: 0.8rem;
                 
            }
            .rmbNumb{
//              color: #FF8C24;
                
            }
        
    }
    .item-nump{
        position: absolute;
        right: 1.4rem;
        font-size: 0.6rem;
        color: #ec4619;
        line-height: 2rem;
        em {
                margin-left:0.2rem;
                font-size: 0.75rem;
            }
    }

}

/*商品详情页商品规格优惠券公共样式*/
.detail-coupons,
.detail-style {
    margin: 0.5rem 0;
    ul{
    	border-top: none !important;
    	border-bottom: 0.05rem solid #d9d9d9;
    }
    .item-icon {
        padding-right: 0;
    }
}
/*商品详情页商家信息*/
.detail-users {
    margin: 0.5rem 0;
    box-shadow: none;
    border-radius: 0;
    .userInfo {
        padding: 0.45rem;
        border-bottom: 0.05rem solid #d9d9d9 !important;
        .item-content {
            padding: 0;
            .item-media {
                width: 4.2rem;
                height: 4.2rem;
                padding: 0;
                img {
                    width: 4.2rem;
                    height: 4.2rem;
                }
            }
            .item-title {
                padding: 0;
                font-size: 0.75rem;
            }
            .item-subtitle{
                 @extend %two-line;
            }
        }
    }
    .detail-tro{
    	margin-left: 0.75rem;
	    margin-top: 0.15rem;
	    width: 100%;
    	align-self: flex-start;
    }
}

.detail-inc{
       //@extend %two-line;
       @include multi-row(3)
       font-size: 0.55rem;
       margin-top: 0.1rem;
       padding-right: 0.45rem ;
       color: #212121;
       line-height: .955rem;
    }
/*商品详情页购买须知*/
.detail-notice {
    //height: 11rem;
    background: #FFFFFF;
    margin-bottom: 0.5rem;
    border-bottom: 0.05rem #D9D9D9 solid;
    padding: 0.45rem 0.6rem 0.1rem 0.5rem;
    font-size: 0.6rem;

    .listOne{
       height: 1.35rem;
       .detail-tel{
       	margin-left: 1rem;
       } 
    }
    .noticeList {
       
        .item-title {
            @extend %clearfix;
            .noticeTitle {
            	@extend %two-line;
                float: left;
                color: #909090;
                width: 3.25rem;
                font-size: 0.6rem;
                line-height: 1.1rem;
            }
            .cont {
              margin-left: 3.25rem;
              color: #212121;
              line-height: 1.1rem;
              font-size: 0.6rem;
            }
        }
    }
}


//商品详情，商品参数、评价tab 吸顶效果
.swiper-wrapper .swiper-slide{
    display: block;
}
//ios
.goodsDetail-tab {
    .tab-link{
        
    	font-size: 0.7rem;
    	padding:0 1rem;
    }
    .button.active {             
        color: #ec4619;
        text-align: center;
        
        .tabLine{
            height: 100%;
             
            span{
            	border-bottom: 0.1rem #ec4619 solid;
            	display: inline-block;
            	height: 100%;
            }             
        }
    }
}

//Android
.tabFixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
}

//商品详情切换
.detail-tab{
	margin-bottom: 2.25rem;
	.swiper-slide{
	    height: 0.5rem;
	}
	.swiper-slide.active{
	    height: auto;
	}
}
