/*商品详情页底部导航*/
.detail-nav{
    height: 2.25rem;
    border-top: 0.05rem solid  #d9d9d9;
    width: 100%;
    
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 9000;
        padding: 0;
    table-layout: fixed;
    border-bottom: 0;
    border-left: 0;
    transition-duration: .4s;
        background-color: #f5f5f5;
    //display: table-row;
    
    i{
    	width: 1rem !important;
        height: 1.1rem !important;
        font-size: 1rem !important;
        line-height: 1rem !important;
        padding: 0rem 0 !important;
    }
    .detail-navitem{
        width: 15%;
        margin: 0;
        border-right: 0.05rem solid #d9d9d9;
        background-color: #f5f5f5;
        font-size: 0.5rem;
        color: #212121 ;
        vertical-align:top;
        padding: .2rem 0 0 0;
        display: inline-block;
        text-align: center;
        
    }
    .detail-col{
    	width:12.5%;
        margin: 0;
        background-color: #f5f5f5;
        font-size: 0.5rem;
        color: #212121 ;
        vertical-align:top;
        padding: .2rem 0 0 0;
        display: inline-block;
        text-align: center;
    }
    .detail-buy{
        width: 36%;
        margin: 0;
        color: #fff;
        background-color: #ff8c24 ;
        font-size: 0.85rem;
        padding: 0.5rem 0 0.6rem 0;
        vertical-align: baseline;
        display: inline-block;
        text-align: center;
    }
    .detail-change{
       width: 36.5%;
        margin: 0;
        background-color: #ec4619 ;
        color: #fff;
        font-size: 0.85rem;
        padding: 0.5rem 0 0.6rem 0;
        vertical-align: baseline;
        position: absolute;
        display: inline-block;
        text-align: center;
    }
}

.gd-footer{
    position: absolute;
    bottom: 0;
    height: 2.25rem;
    line-height: 2.25rem;
    text-align: center;
    color: #FFFFFF;
    width: 100%;
    font-size: 0.85rem;
    .gd-shopCart{
        background-color: #ff8c24;
        width: 50%;
        float: left;
        position: initial;
    }
    .gd-convert{
        background-color: #ec4619;
        width: 50%;
        float: left;
        position: initial;
    }
}
